.progress-bar[value] {
  /* Reset the default appearance */
  -webkit-appearance: none;
  appearance: none;

  width: 250px;
  height: 20px;

  &::-webkit-progress-bar {
    background: rgba(217, 217, 217, 0.15);
    border-radius: 12px;
    box-shadow: none;
  }
}

.Ihw2 {
  position: fixed;
  bottom: 20px;
  left: 14px;
  right: 14px;
}

.progress-bar {
  height: 16px;
  border-radius: 20px;
  overflow: hidden;
  background-color: #fff3;
  flex-grow: 1;
  &__bottom {
    position: fixed;
    bottom: 20px;
    left: 14px;
    right: 14px;
  }
  &__line {
      height: 16px;
      border-radius: 20px;
      width: 0px;
      transition: width 0.45s ease 0.2s, opacity 0.45s ease 0.2s;
      opacity: 0;
      will-change: opacity, width;
  }
}

.progress-bar-color {
  &__type-bronze {
      background: linear-gradient(270deg, #fefbe9 0%, #ff9a00 100%);
  }
  &__type-silver {
      background: linear-gradient(270deg, #eff9ff 0%, #4a57a7 100%);
  }
  &__type-gold {
      background: linear-gradient(270deg, #fffcd2 0%, #f6c35e 100%);
  }
  &__type-platinum {
      background: linear-gradient(270deg, #dae3ff 0%, #696c7f 100%);
  }
  &__type-diamond {
      background: linear-gradient(270deg, #9ffeff 0%, #2792e2 100%);
  }
}
