.grey-transparent-btn {
  border-radius: 14px;
  backdrop-filter: blur(14px);
  background: rgba(255, 255, 255, 0.12);
  margin-top: 38px;
  &:active {
    background-color: #29292c;
  }
  &_with-icon {
    svg {
      flex-shrink: 0;
      color: rgb(112, 117, 121);
    }
  }
}

.boost-page {
  &__header-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding: 16px 12px;
    width: 100px;
    margin-top: 23px;
  }
  &__header-btn-content {
    display: flex;
    align-items: center;
  }
}

.boostIcon {
  font-size: 36px;
}

.IconBackground {
  width: 70px;
  height: 70px;
  background-color: var(--palette-transparent-white-05);
  border-radius: 12px;
  margin-left: 4px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.boost-icon {
  font-size: 36px;

  &__background {
    width: 70px;
    height: 70px;
    background-color: var(--palette-transparent-white-05);
    border-radius: 12px;
    margin-left: 4px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

._coinIcon {
  margin-left: 5px;
  margin-top: 2px;
  width: 16px;
  height: 16px;
  background-image: url("../../assets/images/penny.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-color: transparent;
}
